@import url(https://fonts.googleapis.com/css2?family=Nanum+Gothic&display=swap);
body {
  margin: 0;
  font-family: 'Nanum Gothic', sans-serif; 
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background:#f0f0f0
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

main{
    width:500px;
    position: absolute;
    left:50%;
    top:50%;
    transform:translate(-50%,-50%);
    background: #fff;
    box-shadow: 3px 3px 8px 5px rgba(0,0,0,0.1);
}
.palette-wrapper{
    width:100%;
    padding:0 5%;
    box-sizing: border-box;
    text-align: center;
    margin:1rem 0;
}

.form-wrapper{
    width:100%;
    padding:0 5%;
    box-sizing: border-box;
}

.list-wrapper{
    margin:1rem 0;
    height:500px;
    overflow-y: scroll;
}
.color-data{
    width:2rem;
    height:2rem;
    border-radius: 1rem;
    margin:1rem 0.5rem;
    display: inline-block;
    opacity: 0.4;
    transition: opacity 0.3s
}

.color-data:hover{
    opacity: 1;
}

.color-data.active{
    opacity:1
}


.form{
    display: flex;
    width:100%
}

.form input{
    flex:1 1;
    border:none;
    border-bottom:1px solid #a0a0a0;
}

.form .create-button{
    width:3rem;
    height:2em;
    border-radius: 0.5rem;
    text-align: center;
    line-height: 2rem;
    margin-left: 1rem;
    color:#fff
}
.todo-item{
    display: flex;
    cursor:pointer;
    padding: 1rem;
}

.todo-text{
    flex:1 1;
    line-height: 2rem;
}

.remove,.edit{
    opacity:0;
    line-height: 2rem;
    color:#888888;
}
.edit{
    margin-right:1rem
}
.todo-text.checked{
    color:#888 !important;
    text-decoration: line-through;
}

.todo-item:hover{
    background: rgba(0,0,0,0.05);
}

.todo-item:hover .remove,
.todo-item:hover .edit{
    opacity:1
}

.checked-circle{
    border:1px solid #a0a0a0;
    width:2rem;
    height:2rem;
    line-height: 2rem;
    text-align: center;
    border-radius: 1rem;
    color:#a0a0a0;
    margin-right:1rem
}

.checked-circle.checked{
    border-color:#343a40;
    color:#343e40;
    background-color: #343a40;
}
